import React from 'react'
import styled, { keyframes } from 'styled-components'
import Typing from 'react-typing-animation'
import Layout from '../components/layout'
import SEO from '../components/seo'
import Logo from '../components/brandImage'
import { Link, StaticQuery, graphql } from 'gatsby'
import Img from "gatsby-image"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" keywords={[`gatsby`, `application`, `react`]} />
    <div style={{marginLeft: '10%'}}>
      <div style={{minHeight: '400px'}}>
        <img
          src="/static/e83bb50724cc5b10eacede766b75d3ba/b3372/profile.png"
          style={{
            width: 250,
            height: 300,
            backgroundColor: "white",
            float: "left",
            border: "2px solid white"
            }}
        />
        <div style={{display: 'inline-block', marginLeft: "20px", marginTop: "20px"}}>
          <p style={{color: 'red', fontSize:'2rem', textShadow:'2px 2px 1px yellow'}}>Hi,</p>
          <Typing speed={100} loop={true} cursor={<Cursor className="red-cursor" />}>
            <p>Myself, <span style={{color: 'yellow', fontSize:'2rem', textShadow:'2px 2px 0px red'}}>Kirankumar Ambati.</span></p>
            <span>I'm a </span>
            <span style={{color:'#0be61c'}}>Designer.</span>
            <Typing.Backspace count={9} delay={2000}  />
            <span style={{color: 'red'}}>Developer.</span>
            <Typing.Backspace count={10} delay={2000} />
            <span style={{color: "#32fdce"}}>Mentor.</span>
            <Typing.Backspace count={8} delay={2000} />
            <span style={{color: 'yellow'}}>Teacher.</span>
            <Typing.Reset delay={3000} />
          </Typing>
        </div>
      </div>
      
      <div>
        <a href="tel:+919100704096"><i className="fa fa-phone icon" style={{paddingLeft: 0}}></i></a>
        <a href="mailto: ambatikirankumar4@gmail.com"><i className="fa fa-envelope-o icon"></i></a>
        <a href="https://www.linkedin.com/in//ambatikirankumar"><i className="fa fa-linkedin icon"></i></a>
        <a href="https://www.twitter.com/_sudo_user"><i className="fa fa-twitter icon"></i></a>
        <a href="https://github.com/KirankumarAmbati/"><i className="fa fa-github icon"></i></a>
      </div>
      {/* <Logo /> */}
      {/* <ProfileImage /> */}
    </div>
  </Layout>
)

const blink = keyframes`
  from, to {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
`;

const CursorSpan = styled.span`
  font-weight: 100;
  color: red;
  font-size: 1.5em;
  padding-left: 2px;
  animation: ${blink} 1s step-end infinite;
`;

const Cursor = () => (
  <CursorSpan>|</CursorSpan>
);

const ProfileImage = () => (
  <StaticQuery
    query={graphql`
      query {
        placeholderImage: file(relativePath: { eq: "profile.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 3000) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => <Img fluid={data.placeholderImage.childImageSharp.fluid} style={{height:550, width:550}} />}
  />
)

export default IndexPage
