import React from 'react'
import { Link, StaticQuery, graphql } from 'gatsby'
import Img from "gatsby-image"
import Logo from '../components/brandImage'
const activeClass = {
  backgroundColor: 'yellow',
  color: 'black',
  
};

const Header = ({ siteTitle }) => (
  <div style={{backgroundColor:"black", color: "yellow", padding: 20, minHeight: 100, display: "flex", flex:1, verticalAlign: "center"}}>
    
    {/* <Link to="/"><img src="/static/4c88e90a626d3445e5147276ddb7aade/b3372/k_logo.png" style={{width: 150, height: 100}}/></Link> */}
    {/* <nav style={{display: "flex", flex: 1, justifyContent: "flex-end"}}>
      <ul style={{display: "flex", flex: 1, justifyContent: "space-around", maxWidth: 800}}>
        <Link to="/" activeStyle={activeClass}>Home</Link>
        <Link to="/about"  activeStyle={activeClass}>About Me</Link>
        <Link to="/experience"  activeStyle={activeClass}>My Experience</Link>
        <Link to="/projects"  activeStyle={activeClass}>Projects</Link>
        <Link to="/hire"  activeStyle={activeClass}>Contact Me</Link>
        <Link to="/blog"  activeStyle={activeClass}>Blog</Link>
      </ul>
    </nav> */}

    <a href="#main-menu"
     id="main-menu-toggle"
     className="menu-toggle"
     aria-label="Open main menu">
    <span className="sr-only">Open main menu</span>
    <span className="fa fa-bars" aria-hidden="true" style={{color: 'yellow'}}></span>
  </a>
  
  <nav id="main-menu" className="main-menu" aria-label="Main menu">
    <a href="#main-menu-toggle"
       id="main-menu-close"
       className="menu-close"
       aria-label="Close main menu">
      <span className="sr-only">Close main menu</span>
      <span className="fa fa-close" style={{color: 'red'}} aria-hidden="true"></span>
    </a>
    <ul className="test">
        <Link to="/" activeStyle={activeClass}>Home</Link>
        <Link to="/about"  activeStyle={activeClass}>About Me</Link>
        <Link to="/experience"  activeStyle={activeClass}>My Experience</Link>
        <Link to="/projects"  activeStyle={activeClass}>Projects</Link>
        <Link to="/hire"  activeStyle={activeClass}>Contact Me</Link>
        <Link to="/blog"  activeStyle={activeClass}>Blog</Link>
    </ul>
  </nav>
  <a href="#main-menu-toggle"
     className="backdrop"
     tabIndex="-1"
     aria-hidden="true" hidden></a>
  </div>
)

const BrandImage = () => (
  <StaticQuery
    query={graphql`
      query {
        placeholderImage: file(relativePath: { eq: "k_logo.png" }) {
          childImageSharp {
            fluid(maxWidth: 300) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => <Img fluid={data.placeholderImage.childImageSharp.fluid} />}
  />
)

export default Header